.sidebar{
    flex: 3;
    margin: 20px;
    padding-bottom: 20px;
    background-color: #fbfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarItem{
    display: flex;
    padding: 10px;
    flex-direction: column;
    align-items: center;
    justify-content: baseline;
}

.sidebarItemTitle{
    display:block;
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela', sans-serif;
    font-weight: 600;
    text-align: center;
}

.sidebarItem > img, p{
    margin-top:10px;
}

.sidebarList{
    list-style: none;
}

.sidebarListItems{
    display: inline-block;
    width: 50%;
    margin-top: 10px;
}

.sidebarImg{
    width: 250px;
}