.header{
    margin-top:50px;
}

.headerTitles{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', serif;
    color: #444444;
}

.headerTitleSm{
    position: absolute;
    top: 13%;
    font-size: 20px;
}
.headerTitleLg{
    position: absolute;
    top: 15%;
    font-size: 100px;
}

.headerImg{
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
}