.write{
    padding-top: 50px;
}

.writeForm{

}

.writeImg{
    width: 70vw;
    height: 250px;
    border-radius: 10px;
    margin-left: 15vw;
    object-fit: cover;
}
.writeFormGroup{
    margin-left: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.writeFormIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: rgb(160, 155, 155);
    cursor: pointer;
}

.writeInput{
    font-size: 30px;
    border: none;
    padding: 10px;
    width: 70vw;
    height: 10vw;
}

.writeInput:focus{
    outline: none;
}

.writeText{
    font-size: 20px;
}

.writeSubmit{
    margin-left: 15vw;
    color: white;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
}