.post{
    width: 400px;
    margin: 30px 15px 10px 20px;
}

.postImg{
    object-fit: cover;
    width:100%;
    height: 280px;
    border-radius: 10px;
}

.postCategories{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Varela', sans-serif;
    color: #be9656;
    font-size: 11px;
}

.potTitle{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin-top: 11px;
    cursor: pointer;
}